.dropdown-menu-page {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 57px;
  right: 0;
  bottom: 0;
  left: 1rem;
  z-index: 2;
}

@media (min-width: 1000px) {
  .dropdown-menu-page {
    display: none;
  }
}
.headerContainer {
  height: 56px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-flow: row wrap;
  background-color: #fff;
  border-bottom: 1px solid rgb(203, 203, 203);
}

.title {
  color: green;
  margin: 0px;
}

html {
  font-size: 16px;
}

body {
  margin-left: 1rem;
  margin-right: 1rem;
}

@media (min-width: 1000px) {
  .mainContainer {
    display: flex;
  }
}